
@function vw($vw) {
  @return calc(#{$vw / 100} * (100vw - 20px));
}

body {
  margin: 0;
  font-family: 'Favorit', Helvetica, sans-serif;
  font-feature-settings: "ss03", "zero";
  height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr;
  overflow: hidden;

  a {
    color: currentColor;
    text-decoration: none;

    &:hover {
      font-family: 'Favorit Lining', Helvetica, sans-serif;
    }
  }
}

header {
  display: grid;
  grid-template-columns: 0.618fr 1fr 1fr;
  padding: 10px;

  * {
    font-size: vw(1.25);
    line-height: vw(1.5);
    font-weight: normal;
    margin: 0;
    padding: 0;
  }

  .links {
    list-style: none;

    li {
      a {
        text-decoration: none;

        &:hover::after {
          content: 'â†—';
        }
      }
    }
  }
}

main {
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr 1.618fr 1fr 0.618fr;
  grid-template-rows: 1fr auto;

  .intro {
    margin: 0;
    grid-column: 1 / span 2;
    grid-row: 2;
    font-size: vw(2.5);
    line-height: vw(2.5);
    align-self: flex-end;
  }

  .gradient {
    grid-column: 4;
    grid-row: 2;
    border-radius: 800px 800px 0px 00px;
    background: linear-gradient(-45deg, rgba(255,255,0,1) 0%, rgba(255,0,255,1) 33%, rgba(0,255,255,1) 66%, rgba(255,255,0,1) 100%);
    background-size: 100% 100%;
    background-position: center;
  }
}
